:root{
    --color: #0f0;
    --bg: gray;
    font-size: 12px;
}

*{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

h1{
    display: flex;
    justify-content: center;
    align-items: center;
    color: var(--color);
    background: var(--bg);
}

.copyright{
    color: #fff;
    background: #ccc;
    font-size: 1.5rem;
}
